export default {
	data() {
		return {
			Router:this.$Router,
			showView: false,
			is_show:false,
			show_addok:false,
			user_no:'',
			isShare:false,
			mainData:{},
			num:'',
			logData:[]
		}
	},
	
	
	
	onLoad(options) {
		const self = this;
		self.id = options.id;
		if (options.user_no) {
			self.user_no = options.user_no;
			if(self.user_no!=uni.getStorageSync('user_info').user_no){
				self.isShare =true
			}
		}else{
			self.user_no = uni.getStorageSync('user_info').user_no;
		}

		self.$Utils.loadAll(['getMainData'], self);
	},
	
	methods: {
		
		goBuy(type) {
			const self = this;
			var orderList = {
				sku: [{
					id: self.mainData.id,
					sku: self.mainData
				}],
				type: self.mainData.type,
			};
			uni.setStorageSync('payPro', orderList);
			self.$Router.navigateTo({
				route: {
					path: '/pages/confirmOrder/confirmOrder?type=' + type + '&order_id='
					+ self.mainData.order[0].id+'&price='+self.mainData.order[0].price
				}
			})
		},
		
		onShareAppMessage: function(ops) {
			console.log(ops)
			const self = this;
			if (ops.from === 'button') {
				// 来自页面内转发按钮
				return {
					title: '帮我砍一刀~~',
					imageUrl: self.mainData.mainImg[0].url,
					desc: '小蜂拼拼社区团购',
					path: '/pages/game_bargainDetailTwo/game_bargainDetailTwo?id='+self.mainData.id+'&user_no='+self.user_no, //点击分享的图片进到哪一个页面
					success: function(res) {
						// 转发成功
						console.log("转发成功:" + JSON.stringify(res));
					},
					fail: function(res) {
						// 转发失败
						console.log("转发失败:" + JSON.stringify(res));
					}
				}
				console.log(ops.target)
			}else{
				return {
					title: self.mainData.title,
					imageUrl: self.mainData.mainImg[0].url,
					desc: '小蜂拼拼社区团购',
					path: '/pages/game_bargainDetailTwo/game_bargainDetailTwo?id='+self.mainData.id, //点击分享的图片进到哪一个页面
					success: function(res) {
						// 转发成功
						console.log("转发成功:" + JSON.stringify(res));
					},
					fail: function(res) {
						// 转发失败
						console.log("转发失败:" + JSON.stringify(res));
					}
				}
				console.log(ops.target)
			}
			
		},
		
		goKanJia(){
			const self = this;
			self.is_show = !self.is_show
		},
		
		prev(){
			const self = this;
			self.$Router.switchTab({
				route: {
					path: '/pages/index/index'
				}
			})
		},	
		
		kanjia(){
			const self = this;
			var max = parseFloat(self.mainData.o_price);
			var min = parseFloat(self.mainData.price);
			var num = parseFloat(Math.random()*(max-min+1)+min,10)/2;
			self.num = parseFloat(num).toFixed(2)
			self.newPrice = (parseFloat(self.mainData.order[0].price) - parseFloat(num)).toFixed(2)
			if(parseInt(self.mainData.people)<=self.logData.length){
				self.$Utils.showToast('已达到限制人数', 'none');
			}else{
				if(parseInt(self.mainData.people)-self.logData.length==1){
					self.num =(parseFloat(self.mainData.order[0].price) - parseFloat(max)).toFixed(2)
					self.newPrice = max
				};
				self.orderUpdate()
			}
		},
		
		orderUpdate() {
			const self = this;
			const postData = {};
			postData.data = {
				price:self.newPrice
			};
			postData.searchItem  = {
				id:self.mainData.order[0].id,
				user_type:0
			};
			postData.tokenFuncName = 'getProjectToken';
			console.log('postData',postData)
			postData.saveAfter = [
				{
					tableName: 'Log',
					FuncName: 'add',
					data: {
						type: 5,
						relation_id:self.mainData.order[0].id,
						relation_table:'order',
						trade_info:'砍价记录',
						user_no:uni.getStorageSync('user_info').user_no,
						thirdapp_id:2,
						price:self.num
					}
				}
			];
			const callback = (res) => {
				if(res.solely_code==100000){
					self.is_show = true;
					self.getMainData()
				}else{
					self.$Utils.showToast(res.msg, 'none');
				}
			};
			self.$apis.orderUpdate(postData, callback);
		},
		
		getMainData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				type: 3,
				id: self.id
			};
			postData.getAfter = {
				
				order: {
					token: uni.getStorageSync('user_token'),
					tableName: 'Order',
					middleKey: 'product_no',
					key: 'product_no',
					searchItem: {
						status: 1,
						type:3,
						user_no:self.user_no
					},
					condition: '='
				},
				log: {
					tableName: 'Log',
					middleKey: ['order',0,'id'],
					key: 'relation_id',
					searchItem: {
						status: 1,
						type: 5,
						relation_table:'order'
					},
					condition: '='
				},
				logMe: {
					tableName: 'Log',
					middleKey: ['order',0,'id'],
					key: 'relation_id',
					searchItem: {
						status: 1,
						type: 5,
						relation_table:'order',
						user_no:uni.getStorageSync('user_info').user_no
					},
					condition: '='
				},
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData = res.info.data[0];
					const regex = new RegExp('<img', 'gi');
					self.mainData.content = self.mainData.content.replace(regex, `<img style="max-width: 100%;"`);
				} else {
					self.$Utils.showToast('没有更多了', 'none');
				};
				self.getLogData()
				console.log('self.mainData', self.mainData)
			};
			self.$apis.productGet(postData, callback);
		},
		
		getLogData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				type:5,
				relation_id:self.mainData.order[0].id,
				user_type:0
			};
			postData.tokenFuncName = 'getProjectToken';
			postData.getAfter = {	
				user: {
					tableName: 'User',
					middleKey: 'user_no',
					key: 'user_no',
					searchItem: {
						status: 1,
					},
					condition: '='
				},
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.logData.push.apply(self.logData,res.info.data)
				} 
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.logGet(postData, callback);
		},
		
		
	},
};
